import request from "../helpers/request"
import {friendData} from "../helpers/util"

const URL = {
  GET: '/notebooks',
  POST: '/notebooks',
  PATCH: '/notebooks/:id',
  DELETE: '/notebooks/:id'
}

export default {
  getNotebook() {
    return new Promise((resolve, reject) => {
      request(URL.GET)
        .then(res => {
            res.data = res.data.sort((notebook1, notebook2) => notebook1.createdAt < notebook2.createdAt)
            res.data.forEach(notebook => {
              notebook.createdAtFriendly = friendData(notebook.createdAt)
              notebook.updatedAtFriendly = friendData(notebook.updatedAt)
            })
            resolve(res)
          }
        ).catch(err => {
          reject(err)
        }
      )
    })
  },
  postNotebook({title = ''} = {title: title}) {
    return new Promise((resolve, reject) => {
      request(URL.POST, 'POST', {title})
        .then(res => {
          res.data.createdAtFriendly = friendData(res.data.createdAt)
          res.data.updatedAtFriendly = friendData(res.data.updatedAt)
          resolve(res)
        }).catch(err => {
          reject(err)
      })
    })
  },
  patchNotebook(notebookId, {title = ''} = {title: title}) {
    return request(URL.PATCH.replace(':id', notebookId), 'PATCH', {title})
  },
  deleteNotebook(notebookId) {
    return request(URL.DELETE.replace(':id', notebookId), 'DELETE')
  },
}



