import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

const router = new Router({
  routes: [
    {
      path: '/',
      alias:'/notebooks',
      component: ()=>import('../../src/components/notebookList.vue')
    },
    {
      path: '/login',
      component: ()=>import('../../src/components/Login')
    },
    {
      path: '/note',
      component: ()=>import('../../src/components/NoteDetail.vue')
    },
    {
      path: '/trash',
      component: ()=>import('../../src/components/TashDetail.vue')
    },
  ]
})


export default router



